<template>
    <v-row no-gutters class="mb-12">
        <v-col
                :tile="$vuetify.breakpoint.smAndDown ? true : false"
                cols="12"
                xs="12"
                sm="12"
                md="8"
                lg="8"
                class="mx-auto"
        >
            <v-breadcrumbs :items="items">
                <template v-slot:divider>
                    <v-icon>mdi-chevron-right</v-icon>
                </template>
            </v-breadcrumbs>
        <v-stepper v-model="e6" vertical>
            <div class="text-center mt-4 font-18 font-weight-bold">
                <span>ໃບສະໝັກເປັນ Freelance</span>
            </div>
            <v-stepper-step :complete="e6 > 1" step="1">
                ຊື່ ແລະ ນາມສະກຸນ
            </v-stepper-step>

            <v-stepper-content step="1">
                <v-card outlined class="mb-5" >
                  <div class="pa-5">
                      <v-text-field
                              v-model="nickname"
                              class="mb-4"
                              hide-details
                              outlined
                              dense
                              label="ຊື່ຫຼິ້ນ"
                      ></v-text-field>
                      <v-text-field
                              v-model="name"
                              class="mb-4"
                              hide-details
                              outlined
                              dense
                              label="ຊື່ກາງ"
                      ></v-text-field>
                      <v-text-field
                              v-model="lastname"
                              hide-details
                              outlined
                              dense
                              label="ນາມສະກຸນ"
                      ></v-text-field>
                  </div>
                </v-card>
                <v-btn color="primary" @click="e6 = 2">Continue</v-btn>
                <v-btn text>Cancel</v-btn>
            </v-stepper-content>

            <v-stepper-step :complete="e6 > 2" step="2">
                ຂໍ້ມູນຕິດຕໍ່
            </v-stepper-step>

            <v-stepper-content step="2">
                <v-card outlined class="mb-5" >
                    <div class="pa-5">
                        <v-text-field
                                v-model="phone"
                                class="mb-4"
                                hide-details
                                outlined
                                dense
                                label="ເບີໂທ"
                        ></v-text-field>
                        <v-text-field
                                v-model="village"
                                class="mb-4"
                                hide-details
                                outlined
                                dense
                                label="ບ້ານ"
                        ></v-text-field>
                        <v-text-field
                                v-model="district"
                                class="mb-4"
                                hide-details
                                outlined
                                dense
                                label="ເມືອງ"
                        ></v-text-field>
                        <v-text-field
                                v-model="province"
                                hide-details
                                outlined
                                dense
                                label="ແຂວງ"
                        ></v-text-field>
                    </div>
                </v-card>
                <v-btn color="primary" @click="e6 = 3">Continue</v-btn>
                <v-btn text>Cancel</v-btn>
            </v-stepper-content>

            <v-stepper-step :complete="e6 > 3" step="3">
                ຄວາມສາມາດ ແລະ ປະຫວັດການເຮັດວຽກ
            </v-stepper-step>

            <v-stepper-content step="3">
                <v-card outlined class="mb-5" >
                    <div class="pa-5">
                        <v-textarea
                                v-model="skill"
                                class="mb-4"
                                label="ຄວາມສາມາດ ແລະ ທັກສະ"
                                hide-details
                                outlined
                                dense
                        ></v-textarea>
                        <v-textarea
                                v-model="workHistory"
                                label="ປະຫວັດການເຮັດວຽກ"
                                hide-details
                                outlined
                                dense
                        ></v-textarea>
                    </div>
                </v-card>
                <v-btn color="primary" @click="e6 = 4">Continue</v-btn>
                <v-btn text>Cancel</v-btn>
            </v-stepper-content>

            <v-stepper-step step="4">ກວດສອບຂໍ້ມູນຄືນ</v-stepper-step>
            <v-stepper-content step="4">
                <v-card outlined class="mb-5">
                    <div class="pa-5">
                        <p>ຊື່ຫຼິ້ນ: {{ nickname }}</p>
                        <p>ຊື່ກາງ: {{ name }}</p>
                        <p>ນາມສະກຸນ: {{ lastname }}</p>
                        <p>ເບີໂທ: {{ phone }}</p>
                        <p>ບ້ານ: {{ village }}</p>
                        <p>ເມືອງ: {{ district }}</p>
                        <p>ແຂວງ: {{ province }}</p>
                        <p>ຄວາມສາມາດ: {{ skill }}</p>
                        <p>ປະຫວັດການເຮັດວຽກ: {{ workHistory }}</p>
                    </div>
                </v-card>
                <small>#ກະລຸນາກວດສອບຂໍ້ມູນໃຫ້ຖືກຕ້ອງກ່ອນສົ່ງໃບສະໝັກ</small>
                <br>
                <v-btn @click="freelanceRegister" color="primary">ສົ່ງໃບສະໝັກ</v-btn>
                <v-btn text>ຍົກເລີກ</v-btn>
            </v-stepper-content>
        </v-stepper>
        </v-col>
        <app-overlay v-if="overlay"></app-overlay>
    </v-row>
</template>

<script>
    import { mapGetters, mapActions } from 'vuex'
    import appOverlay from '../../components/loading/Overlay'
    export default {
        components: {
          appOverlay
        },
        data: () => ({
            e6: 1,
            nickname: '',
            name: '',
            lastname: '',
            phone: '',
            village: '',
            district: '',
            province: '',
            skill: '',
            workHistory: '',
            overlay: false,
            items: [
                {
                    text: 'Dashboard',
                    to: { name: 'Dashboard' },
                    exact: true
                },
                {
                    text: 'Freelance register',
                }
            ],
        }),
        computed: {
          ...mapGetters([
              'auth'
          ])
        },
        methods: {
            ...mapActions([
                'fetchAuth'
            ]),
           async freelanceRegister () {
              try {
                  this.overlay = true
                  await this.$axios.put('profile/freelance-register', {
                      authId: this.auth._id,
                      nickname: this.nickname,
                      name: this.name,
                      lastname: this.lastname,
                      phone: this.phone,
                      village: this.village,
                      district: this.district,
                      province: this.province,
                      skill: this.skill,
                      workHistory: this.workHistory
                  })
                  this.overlay = false
                  await this.fetchAuth()
                  this.$router.push({ name: 'Dashboard' })
              } catch (e) {
                  throw new Error(e)
              }
            }
        }
    }
</script>
